import Vue from 'vue';
import Vuetify from 'vuetify/lib/framework';


Vue.use(Vuetify);

export default new Vuetify({
    theme:{
        themes:{
            light: {
                primary: '#2EB85C',
                secondary: '#b0bec5',
                accent: '#8c9eff',
                error: '#b71c1c',
                background:'#E5E5E5'
            }
        },
        options: {
            customProperties: true
        }
    }
});
